
<template>
	<div class="edit_view" :style='{}'>
        <div class="breadcrumb-wrapper" style="width: 100%;">
            <div class="bread_view">
                <el-breadcrumb separator="Ξ" class="breadcrumb">
                    <el-breadcrumb-item class="first_breadcrumb" :to="{ path: '/' }">首页</el-breadcrumb-item>
                    <el-breadcrumb-item class="second_breadcrumb" v-for="(item,index) in breadList" :key="index">{{item.name}}</el-breadcrumb-item>
                </el-breadcrumb>
            </div>
        </div>
		<el-form ref="formRef" :model="form" class="add_form" label-width="120px" :rules="rules">
			<el-row>
				<el-col :span="12">
					<el-form-item label="饮品名称" prop="yinpinmingcheng">
						<el-input class="list_inp" v-model="form.yinpinmingcheng" placeholder="饮品名称"
							 type="text" 							:readonly="!isAdd||disabledForm.yinpinmingcheng?true:false" />
					</el-form-item>
				</el-col>

				<el-col :span="12">
					<el-form-item label="饮品类型" prop="yinpinleixing">
						<el-input class="list_inp" v-model="form.yinpinleixing" placeholder="饮品类型"
							 type="text" 							:readonly="!isAdd||disabledForm.yinpinleixing?true:false" />
					</el-form-item>
				</el-col>

				<el-col :span="12">
					<el-form-item label="供应商" prop="gongyingshang">
						<el-input class="list_inp" v-model="form.gongyingshang" placeholder="供应商"
							 type="text" 							:readonly="!isAdd||disabledForm.gongyingshang?true:false" />
					</el-form-item>
				</el-col>

				<el-col :span="12">
					<el-form-item label="采购价格" prop="caigoujiage">
						<el-input class="list_inp" v-model.number="form.caigoujiage" placeholder="采购价格"
							 type="number" 							:readonly="!isAdd||disabledForm.caigoujiage?true:false" />
					</el-form-item>
				</el-col>

				<el-col :span="12">
					<el-form-item label="采购数量" prop="caigoushuliang">
						<el-input class="list_inp" v-model.number="form.caigoushuliang" placeholder="采购数量"
							 type="text" 							:readonly="!isAdd||disabledForm.caigoushuliang?true:false" />
					</el-form-item>
				</el-col>

				<el-col :span="12">
					<el-form-item label="计划时间" prop="jihuashijian">
						<el-date-picker
							class="list_date"
							v-model="form.jihuashijian"
							format="YYYY-MM-DD HH:mm:ss"
							value-format="YYYY-MM-DD HH:mm:ss"
							type="datetime"
							style="width:100%;"
							:readonly="!isAdd||disabledForm.jihuashijian?true:false"
							placeholder="请选择计划时间" />
					</el-form-item>
				</el-col>
				<el-col :span="12">
					<el-form-item label="店长账号" prop="dianzhangzhanghao">
						<el-input class="list_inp" v-model="form.dianzhangzhanghao" placeholder="店长账号"
							 type="text" 							:readonly="!isAdd||disabledForm.dianzhangzhanghao?true:false" />
					</el-form-item>
				</el-col>

				<el-col :span="12">
					<el-form-item label="店铺名称" prop="dianpumingcheng">
						<el-input class="list_inp" v-model="form.dianpumingcheng" placeholder="店铺名称"
							 type="text" 							:readonly="!isAdd||disabledForm.dianpumingcheng?true:false" />
					</el-form-item>
				</el-col>

			</el-row>
			<div class="formModel_btn_box">
				<el-button class="formModel_cancel" @click="backClick">取消</el-button>
				<el-button class="formModel_confirm"
                           @click="save"
                           type="success"
				>
					保存
				</el-button>
			</div>
		</el-form>
	</div>
</template>
<script setup>
	import {
		ref,
		getCurrentInstance,
		watch,
		onUnmounted,
		onMounted,
		nextTick,
		computed
	} from 'vue';
	import {
		useRoute,
		useRouter
	} from 'vue-router';
	const context = getCurrentInstance()?.appContext.config.globalProperties;
	const route = useRoute()
	const router = useRouter()
	//基础信息
	const tableName = 'caigoujihua'
	const formName = '采购计划'
	//基础信息
	const breadList = ref([{
		name: formName
	}])
	//获取唯一标识
	const getUUID =()=> {
      return new Date().getTime();
    }
	//form表单
	const form = ref({
		yinpinmingcheng: '',
		yinpinleixing: '',
		gongyingshang: '',
		caigoujiage: 0,
		caigoushuliang: 0,
		jihuashijian: '',
		dianzhangzhanghao: '',
		dianpumingcheng: '',
	})
	const formRef = ref(null)
	const id = ref(0)
	const type = ref('')
	const disabledForm = ref({
		yinpinmingcheng : false,
		yinpinleixing : false,
		gongyingshang : false,
		caigoujiage : false,
		caigoushuliang : false,
		jihuashijian : false,
		dianzhangzhanghao : false,
		dianpumingcheng : false,
	})
	const isAdd = ref(false)
	//表单验证
	const rules = ref({
		yinpinmingcheng: [
			{required: true,message: '请输入',trigger: 'blur'}, 
		],
		yinpinleixing: [
		],
		gongyingshang: [
		],
		caigoujiage: [
			{ validator: context.$toolUtil.validator.number, trigger: 'blur' },
		],
		caigoushuliang: [
			{ validator: context.$toolUtil.validator.intNumber, trigger: 'blur' },
		],
		jihuashijian: [
		],
		dianzhangzhanghao: [
		],
		dianpumingcheng: [
		],
	})
	//methods

	//methods
	//获取info
	const getInfo = ()=>{
		context?.$http({
			url: `${tableName}/info/${id.value}`,
			method: 'get'
		}).then(res => {
			let reg=new RegExp('../../../file','g')
			form.value = res.data.data
		})
	}
	const crossRow = ref('')
	const crossTable = ref('')
	const crossTips = ref('')
	const crossColumnName = ref('')
	const crossColumnValue = ref('')
	//初始化
	const init = (formId=null,formType='add',formNames='',row=null,table=null,statusColumnName=null,tips=null,statusColumnValue=null) => {
		if(formId){
			id.value = formId
			type.value = formType
		}
		if(formType == 'add'){
			isAdd.value = true
		}else if(formType == 'info'){
			isAdd.value = false
			getInfo()
		}else if(formType == 'edit'){
			isAdd.value = true
			getInfo()
		}
		else if(formType == 'cross'){
			isAdd.value = true
			// getInfo()
			for(let x in row){
				if(x=='yinpinmingcheng'){
					form.value.yinpinmingcheng = row[x];
					disabledForm.value.yinpinmingcheng = true;
					continue;
				}
				if(x=='yinpinleixing'){
					form.value.yinpinleixing = row[x];
					disabledForm.value.yinpinleixing = true;
					continue;
				}
				if(x=='gongyingshang'){
					form.value.gongyingshang = row[x];
					disabledForm.value.gongyingshang = true;
					continue;
				}
				if(x=='caigoujiage'){
					form.value.caigoujiage = row[x];
					disabledForm.value.caigoujiage = true;
					continue;
				}
				if(x=='caigoushuliang'){
					form.value.caigoushuliang = row[x];
					disabledForm.value.caigoushuliang = true;
					continue;
				}
				if(x=='jihuashijian'){
					form.value.jihuashijian = row[x];
					disabledForm.value.jihuashijian = true;
					continue;
				}
				if(x=='dianzhangzhanghao'){
					form.value.dianzhangzhanghao = row[x];
					disabledForm.value.dianzhangzhanghao = true;
					continue;
				}
				if(x=='dianpumingcheng'){
					form.value.dianpumingcheng = row[x];
					disabledForm.value.dianpumingcheng = true;
					continue;
				}
			}
			if(row){
				crossRow.value = row
			}
			if(table){
				crossTable.value = table
			}
			if(tips){
				crossTips.value = tips
			}
			if(statusColumnName){
				crossColumnName.value = statusColumnName
			}
			if(statusColumnValue){
				crossColumnValue.value = statusColumnValue
			}
		}
		context?.$http({
			url: `${context?.$toolUtil.storageGet('frontSessionTable')}/session`,
			method: 'get'
		}).then(res => {
			var json = res.data.data
			if(json.hasOwnProperty('dianzhangzhanghao') && context?.$toolUtil.storageGet("frontRole")!="管理员"){
				form.value.dianzhangzhanghao = json.dianzhangzhanghao
				disabledForm.value.dianzhangzhanghao = true;
			}
			if(json.hasOwnProperty('dianpumingcheng') && context?.$toolUtil.storageGet("frontRole")!="管理员"){
				form.value.dianpumingcheng = json.dianpumingcheng
				disabledForm.value.dianpumingcheng = true;
			}
            if (localStorage.getItem('autoSave')) {
                localStorage.removeItem('autoSave')
                save()
            }
		})
	}
	//初始化
	//取消
	const backClick = () => {
		history.back()
	}
	//提交
	const save=()=>{
		var table = crossTable.value
		var objcross = JSON.parse(JSON.stringify(crossRow.value))
		let crossUserId = ''
		let crossRefId = ''
		let crossOptNum = ''
		if(type.value == 'cross'){
			if(crossColumnName.value!=''){
				if(!crossColumnName.value.startsWith('[')){
					for(let o in objcross){
						if(o == crossColumnName.value){
							objcross[o] = crossColumnValue.value
						}
					}
					//修改跨表数据
					changeCrossData(objcross)
				}else{
					crossUserId = context?.$toolUtil.storageGet('userid')
					crossRefId = objcross['id']
					crossOptNum = crossColumnName.value.replace(/\[/,"").replace(/\]/,"")
				}
			}
		}
		formRef.value.validate((valid)=>{
			if(valid){
				if(crossUserId&&crossRefId){
					form.value.crossuserid = crossUserId
					form.value.crossrefid = crossRefId
					let params = {
						page: 1,
						limit: 1000, 
						crossuserid:form.value.crossuserid,
						crossrefid:form.value.crossrefid,
					}
					context?.$http({
						url: `${tableName}/page`,
						method: 'get', 
						params: params 
					}).then(res=>{
						if(res.data.data.total>=crossOptNum){
							context?.$toolUtil.message(`${crossTips.value}`,'error')
							return false
						}else{
							context?.$http({
								url: `${tableName}/${!form.value.id ? "save" : "update"}`,
								method: 'post', 
								data: form.value 
							}).then(res=>{
                                context?.$toolUtil.message(`操作成功`,'success')
                                history.back()
							})
						}
					})
				}else{
					context?.$http({
						url: `${tableName}/${!form.value.id ? "save" : "update"}`,
						method: 'post', 
						data: form.value 
					}).then(res=>{
                        context?.$toolUtil.message(`操作成功`,'success')
                        history.back()
					})
				}
			}
		})
	}
	//修改跨表数据
	const changeCrossData=(row)=>{
        if(type.value == 'cross'){
            context?.$http({
                url: `${crossTable.value}/update`,
                method: 'post',
                data: row
            }).then(res=>{})
        }
	}
	onMounted(()=>{
		type.value = route.query.type?route.query.type:'add'
		let row = null
		let table = null
		let statusColumnName = null
		let tips = null
		let statusColumnValue = null
		if(type.value == 'cross'){
			row = context?.$toolUtil.storageGet('crossObj')?JSON.parse(context?.$toolUtil.storageGet('crossObj')):{}
			table = context?.$toolUtil.storageGet('crossTable')
			statusColumnName = context?.$toolUtil.storageGet('crossStatusColumnName')
			tips = context?.$toolUtil.storageGet('crossTips')
			statusColumnValue = context?.$toolUtil.storageGet('crossStatusColumnValue')
		}
		init(route.query.id?route.query.id:null, type.value,'', row, table, statusColumnName, tips, statusColumnValue)
	})
    onUnmounted(()=>{
        Object.keys(localStorage).map(item=>{
            if(item.startsWith('cross')){
                localStorage.removeItem(item)
            }
        })
    })
</script>
<style lang="scss" scoped>
	// 面包屑盒子
	.bread_view {
		:deep(.breadcrumb) {
			.el-breadcrumb__separator {
			}
			.first_breadcrumb {
				.el-breadcrumb__inner {
				}
			}
			.second_breadcrumb {
				.el-breadcrumb__inner {
				}
			}
		}
	}
	// 表单
	.add_form{
		// form item
		:deep(.el-form-item) {
			//label
			.el-form-item__label {
			}
			// 内容盒子
			.el-form-item__content {
				// 输入框
				.list_inp {

				}
				//日期选择器
				.list_date {
				}
			}
		}
	}
	// 按钮盒子
	.formModel_btn_box {
		.formModel_cancel {
		}
		.formModel_cancel:hover {
		}
		
		.formModel_confirm {
		}
		.formModel_confirm:hover {
		}
	}
</style>
<style>
.edit_view {
    width: 1200px;
    margin: 20px auto;
    padding: 20px 20px 20px ;
    background: #fff;
    overflow: hidden;
    border: 0px solid #eee;
    font-size: 16px;
    color:#666;
}
.edit_view .add_form{
    width: 100%;
    padding: 30px;
    border:0px solid #eee
}
.edit_view .add_form .el-form-item{
    margin: 0px 0px 20px;
    display: flex;
}
.edit_view .add_form .el-form-item .el-form-item__label{
    width: 150px;
    background: none;
    text-align: right;
    display: block;
    font-size: 16px;
    color: rgb(51, 51, 51);
    font-weight: 500;
}
.edit_view .add_form .el-form-item .el-form-item__content{
    display: flex;
    justify-content: flex-start;
    align-items: center;
    flex-wrap: wrap;
    width: calc(100% - 120px);
}
.edit_view .add_form .el-form-item .el-form-item__content .list_inp{
    height: 36px;
    line-height: 36px;
    border: 1px solid rgb(221, 221, 221);
    padding: 0px 10px;
    width: 100%;
    box-sizing: border-box;
    background: rgb(255, 255, 255);
    font-size: 16px;
}


.edit_view .add_form .el-form-item .el-form-item__content .list_date{
    line-height: 36px;
    border: 1px solid rgb(221, 221, 221);
    box-sizing: border-box;
    width: 100%;
    border-radius: 0px;
    background: rgb(255, 255, 255);
    font-size: 16px;
}








</style>