import { createRouter, createWebHashHistory } from 'vue-router'
import index from '../views'
import home from '../views/pages/home.vue'
import login from '../views/pages/login.vue'
import newsList from '@/views/pages/news/list'
import messagesList from '@/views/pages/messages/list'
import forumList from '@/views/pages/forum/list'
import yonghuList from '@/views/pages/yonghu/list'
import yonghuDetail from '@/views/pages/yonghu/formModel'
import yonghuAdd from '@/views/pages/yonghu/formAdd'
import yonghuRegister from '@/views/pages/yonghu/register'
import yonghuCenter from '@/views/pages/yonghu/center'
import dianzhangList from '@/views/pages/dianzhang/list'
import dianzhangDetail from '@/views/pages/dianzhang/formModel'
import dianzhangAdd from '@/views/pages/dianzhang/formAdd'
import yuangongList from '@/views/pages/yuangong/list'
import yuangongDetail from '@/views/pages/yuangong/formModel'
import yuangongAdd from '@/views/pages/yuangong/formAdd'
import yinpinfenleiList from '@/views/pages/yinpinfenlei/list'
import yinpinfenleiDetail from '@/views/pages/yinpinfenlei/formModel'
import yinpinfenleiAdd from '@/views/pages/yinpinfenlei/formAdd'
import yinpinxinxiList from '@/views/pages/yinpinxinxi/list'
import yinpinxinxiDetail from '@/views/pages/yinpinxinxi/formModel'
import yinpinxinxiAdd from '@/views/pages/yinpinxinxi/formAdd'
import storeupList from '@/views/pages/storeup/list'
import addressList from '@/views/pages/shop_address/list'
import syslogList from '@/views/pages/syslog/list'
import syslogDetail from '@/views/pages/syslog/formModel'
import syslogAdd from '@/views/pages/syslog/formAdd'
import cartList from '@/views/pages/shop_order/cart'
import caigoujihuaList from '@/views/pages/caigoujihua/list'
import caigoujihuaDetail from '@/views/pages/caigoujihua/formModel'
import caigoujihuaAdd from '@/views/pages/caigoujihua/formAdd'
import dianpufenleiList from '@/views/pages/dianpufenlei/list'
import dianpufenleiDetail from '@/views/pages/dianpufenlei/formModel'
import dianpufenleiAdd from '@/views/pages/dianpufenlei/formAdd'
import order_confirm from '@/views/pages/shop_order/confirm'
import ordersList from '@/views/pages/shop_order/list'

const routes = [{
		path: '/',
		redirect: '/index/home'
	},
	{
		path: '/index',
		component: index,
		children: [{
			path: 'home',
			component: home
		}
		, {
			path: 'newsList',
			component: newsList
		}
		, {
			path: 'messagesList',
			component: messagesList
		}
		, {
			path: 'forumList',
			component: forumList
		}
		, {
			path: 'yonghuList',
			component: yonghuList
		}, {
			path: 'yonghuDetail',
			component: yonghuDetail
		}, {
			path: 'yonghuAdd',
			component: yonghuAdd
		}
		, {
			path: 'yonghuCenter',
			component: yonghuCenter
		}
		, {
			path: 'dianzhangList',
			component: dianzhangList
		}, {
			path: 'dianzhangDetail',
			component: dianzhangDetail
		}, {
			path: 'dianzhangAdd',
			component: dianzhangAdd
		}
		, {
			path: 'yuangongList',
			component: yuangongList
		}, {
			path: 'yuangongDetail',
			component: yuangongDetail
		}, {
			path: 'yuangongAdd',
			component: yuangongAdd
		}
		, {
			path: 'yinpinfenleiList',
			component: yinpinfenleiList
		}, {
			path: 'yinpinfenleiDetail',
			component: yinpinfenleiDetail
		}, {
			path: 'yinpinfenleiAdd',
			component: yinpinfenleiAdd
		}
		, {
			path: 'yinpinxinxiList',
			component: yinpinxinxiList
		}, {
			path: 'yinpinxinxiDetail',
			component: yinpinxinxiDetail
		}, {
			path: 'yinpinxinxiAdd',
			component: yinpinxinxiAdd
		}
        , {
            path: 'storeupList',
            component: storeupList
        }
		, {
			path: 'addressList',
			component: addressList
		}
		, {
			path: 'syslogList',
			component: syslogList
		}, {
			path: 'syslogDetail',
			component: syslogDetail
		}, {
			path: 'syslogAdd',
			component: syslogAdd
		}
		, {
			path: 'cartList',
			component: cartList
		}
		, {
			path: 'caigoujihuaList',
			component: caigoujihuaList
		}, {
			path: 'caigoujihuaDetail',
			component: caigoujihuaDetail
		}, {
			path: 'caigoujihuaAdd',
			component: caigoujihuaAdd
		}
		, {
			path: 'dianpufenleiList',
			component: dianpufenleiList
		}, {
			path: 'dianpufenleiDetail',
			component: dianpufenleiDetail
		}, {
			path: 'dianpufenleiAdd',
			component: dianpufenleiAdd
		}
		, {
			path: 'order_confirm',
			component: order_confirm
		}
		, {
			path: 'ordersList',
			component: ordersList
		}
		]
	},
	{
		path: '/login',
		component: login
	}
	,{
		path: '/yonghuRegister',
		component: yonghuRegister
	}
]

const router = createRouter({
  history: createWebHashHistory(process.env.BASE_URL),
  routes
})

export default router
